import { IObject } from 'src/utils';
import en from './en';
import vi from './vi';

export * from './interface';

export const translateByLanguage = (language: string) => {
    switch (language) {
        case 'en':
            return en;
        case 'vi':
            return vi;
        default:
            return en;
    }
};

export const languages = { en };

